import store from 'store';
import { getConfig } from 'js/fetch';
const getConfigs = async (fn) => {
  let res = await getConfig()
  if (!res) {
    return;
  }
  let hosts = {
    bizHosts: [],
    exHosts: [],
    infoHosts: [],
    coins: [],
  };
  let {
    bizHosts,
    exHosts,
    infoHosts,
    coins
  } = res.data;

  if (Array.isArray(bizHosts) && bizHosts.length > 0) {
    hosts.bizHosts = bizHosts;
  }
  if (Array.isArray(exHosts) && exHosts.length > 0) {
    hosts.exHosts = exHosts;
  }
  if (Array.isArray(infoHosts) && infoHosts.length > 0) {
    hosts.infoHosts = infoHosts;
  }
  store.dispatch("updateHosts", hosts);

  if (Array.isArray(coins)) {
    store.dispatch('updateCoins', coins);
    console.log("getCoins", store.getters.coins);
  }
}

export {
  getConfigs
}


