import axios from 'axios';

const service = axios.create({
  withCredentials: true,
  timeout: 30000
})
const resSuccess = (res) => {
  let response = {};
  if (res.status === 200) {
    let {
      data
    } = res;
    if (data.code === '0') {
      response.result = true;
      response.data = data.data;
      response.code = data.code;
    } else {
      response.result = false;
      response.data = res.data;
      response.code = data.code;
      response.msg = data.msg
    }
    return response
  }
  return {
    result: false,
    msg: res.statusText
  }
}

// server's url and port
const exHosts = process.env.exHosts;
const exPort = process.env.exPort;

// get configuration file from server
export const getConfig = async () => {
  let url = window.location.origin + '/static/config/jc_config.json' + '?t=' + new Date().getTime();
  let res = await service({
    url: url,
    method: "get"
  });
  return resSuccess(res);
}

// get exchange host 
const getExHost = () => {
  let uri = "";
  if (process.env.NODE_ENV === "production") {
    let hosts = store.getters.hosts.exHosts.length === 0 ? exHosts : store.getters.hosts.exHosts;
    uri = "https://" + hosts[0] + ":" + exPort;
  }
  return uri;
}

/** get user balance */
export const getUserBalance = async (address) => {
  let res = await service({
    url: getExHost() + '/exchange/balances/' + address,
    method: 'get'
  })
  return resSuccess(res);
}


